<template>
  <div class="yaoClass">
    <div class="yaoClass-header">
      <h3>药品分类</h3>
    </div>
    <crumbs></crumbs>
    <div class="yao">
    <el-collapse accordion>
      <el-collapse-item v-for="(yao,index) in yaoClass" :key="yao.label">
          <template slot="title">
            {{yao.label}}<i class="header-icon el-icon-information"></i>
          </template>
          <div class="content-two">
            <el-collapse accordion>
              <el-collapse-item v-for="(an,index) in yao.children" :key="an.label">
                <template slot="title">
                  {{an.label}}
                </template>
                <div v-for="(anTwo,index) in an.children" :key="anTwo.label">
                  <router-link  :to="{ name: 'productList', params: { id: anTwo.id }}">{{anTwo.label}}</router-link>
                </div>
              </el-collapse-item>
            </el-collapse>
            </div>
        </el-collapse-item>
      </el-collapse>
    </div>

  </div>
</template>

<script>
import crumbs from "~/components/crumbs.vue";
import { yaoClass } from "~/data/yaoClass";

export default {
  data() {
    return {
      activeName: "1",
      yaoClass: yaoClass
    };
  },
  components: {
    crumbs
  },
  methods: {
    menu() {
      window.scrollTo(0,0);
    }
  },
  created(){
    this.menu();
  }
};
</script>

<style lang="scss" scoped>
.yaoClass {
  font-size:2rem;
  .content-two{
    padding:0 15px;
    .el-collapse{
      border:0;
    }

  }
  
  .yaoClass-header {
    background-color: #461b93;
    color: #fff;
    text-align: center;
    h3 {
      line-height: 100px;
    }
  }
  .crumbs {
    padding: 15px;
  }
  .yao {
    padding: 15px;
  }
  .el-collapse-item__content{
    a{
        background: #eee;
        display: block;
        padding:.5rem;
        padding-left: 1rem;
        margin: 10px 0;
        color:#461b93;
      }
  }
}
</style>



